.event-detail {
  max-width: 1200px;
  margin: 0 auto;
}

.event-header {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 24px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
}

.event-info h1 {
  font-size: 28px;
  font-weight: 700;
  color: #333;
  margin: 0 0 12px 0;
}

.event-description {
  color: #6c757d;
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 16px;
}

.event-meta {
  display: flex;
  gap: 16px;
  align-items: center;
  font-size: 14px;
  color: #6c757d;
}

.event-actions {
  display: flex;
  gap: 12px;
  flex-shrink: 0;
}

.complete-button {
  background: #28a745;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.complete-button:hover {
  background: #218838;
  transform: translateY(-1px);
}

.archive-button {
  background: #6c757d;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.archive-button:hover {
  background: #5a6268;
  transform: translateY(-1px);
}

.participant-input-section {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 24px;
}

.participant-input-section h3 {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0 0 16px 0;
}

.name-input {
  width: 100%;
  max-width: 300px;
  padding: 12px 16px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.name-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.best-times {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 24px;
}

.best-times h3 {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0 0 20px 0;
}

.best-times-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
}

.best-time-slot {
  background: #f8f9fa;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  padding: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.time-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.time-info strong {
  font-size: 16px;
  color: #333;
}

.time-info span {
  font-size: 14px;
  color: #6c757d;
}

.availability-summary {
  display: flex;
  gap: 12px;
  align-items: center;
}

.available-count {
  color: #28a745;
  font-weight: 600;
}

.unavailable-count {
  color: #dc3545;
  font-weight: 600;
}

.availability-grid {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 24px;
  overflow-x: auto;
}

.availability-grid h3 {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0 0 20px 0;
}

.grid-header {
  display: grid;
  grid-template-columns: 200px repeat(auto-fit, minmax(120px, 1fr));
  gap: 8px;
  padding: 12px;
  background: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 8px;
  font-weight: 600;
  color: #333;
}

.grid-body {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.time-slot-row {
  display: grid;
  grid-template-columns: 200px repeat(auto-fit, minmax(120px, 1fr));
  gap: 8px;
  padding: 12px;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  align-items: center;
}

.time-column {
  font-weight: 500;
}

.time-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.time-info strong {
  font-size: 14px;
  color: #333;
}

.time-info span {
  font-size: 12px;
  color: #6c757d;
}

.participant-cell {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
}

.response {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 16px;
}

.response.available {
  background: #d4edda;
  color: #155724;
}

.response.unavailable {
  background: #f8d7da;
  color: #721c24;
}

.response-buttons {
  display: flex;
  gap: 4px;
}

.available-btn,
.unavailable-btn {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: none;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.available-btn {
  background: #d4edda;
  color: #155724;
}

.available-btn:hover:not(:disabled) {
  background: #c3e6cb;
  transform: scale(1.1);
}

.unavailable-btn {
  background: #f8d7da;
  color: #721c24;
}

.unavailable-btn:hover:not(:disabled) {
  background: #f5c6cb;
  transform: scale(1.1);
}

.available-btn:disabled,
.unavailable-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.participants-list {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.participants-list h3 {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0 0 16px 0;
}

.participants-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.participant-tag {
  background: #f8f9fa;
  color: #495057;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
  border: 1px solid #e9ecef;
}

@media (max-width: 768px) {
  .event-header {
    flex-direction: column;
    align-items: stretch;
  }
  
  .event-actions {
    justify-content: flex-start;
  }
  
  .best-times-grid {
    grid-template-columns: 1fr;
  }
  
  .grid-header,
  .time-slot-row {
    grid-template-columns: 150px repeat(auto-fit, minmax(100px, 1fr));
  }
  
  .time-column {
    font-size: 12px;
  }
  
  .time-info strong {
    font-size: 12px;
  }
  
  .time-info span {
    font-size: 10px;
  }
} 